<template>
  <div style="margin: 20px">
    <Form :loading="loading" label-position="left" :label-width="180">
      <template v-for="item in configurationControls">
        <template v-if="item.name=='medal'">
          <FormItem label="是否开启奖章活动">
            <Button type="primary" @click="closeMedal(item)">{{medalBol?'关闭':'开启'}}
            </Button>
          </FormItem>
          <FormItem :label="item.title" :key="item.id"   v-show="medalBol">
            <Input :value="item.scale" disabled size="large"></Input>
            <Button type="primary" @click="editHandler(item)"
              style="position: absolute;right: 0;top:0;height: 100%;width: 100px;">修改
            </Button>
          </FormItem>
        </template>
        <template v-else>
          <FormItem :label="item.title" :key="item.id" >
            <Input :value="item.scale" disabled size="large"></Input>
            <Button type="primary" @click="editHandler(item)"
              style="position: absolute;right: 0;top:0;height: 100%;width: 100px;">修改
            </Button>
          </FormItem>
        </template>
      </template>
    </Form>
    <Modal :title="currentModal.label+'修改'" v-model="currentModal.show" width="300" class-name="vertical-center-modal">
      <InputNumber v-model.trim="currentModal.value" :placeholder="'请输入' + currentModal.label +'！'"
        style="width: 100%"></InputNumber>
      <div slot="footer">
        <Button size="large" @click="currentModal.show=false">取消</Button>
        <Button type="primary" size="large" @click="submitHandler">提交</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        medalBol:true,
        loading: false,
        configurationControls: [],
        currentModal: {
          show: false,
          label: "",
          value: 0,
          id: 0
        }
      };
    },
    created() {
      this.getConfiguration();
    },
    mounted() {
      this.$sitemap.prvname = "系统管理";
      this.$sitemap.currname = "配置信息";
      this.$menuactive.open = ["SystemBlock"];
      this.$menuactive.active = "Setting";
    },
    methods: {
      editHandler(item) {
        this.currentModal.show = true;
        this.currentModal.label = item.title;
        this.currentModal.value = item.scale;
        this.currentModal.id = item.id;
      },
      //得到配置信息
      async getConfiguration() {
        try {
          this.loading = true;
          const result = await this.$http.post(this.$api.get_configuration, {});
          this.loading = false;
          if (result.data.result === "1") {
            this.configurationControls = result.data.data;
            console.log("result.data.data[0]",result.data.data[0])
            console.log("this.configurationControls",this.configurationControls)
            this.getmedalBol();
          }
        } catch (e) {
          this.$Message.error('服务器异常');
          console.log(e);
        }
      },
      //得到medalBol
      getmedalBol(){
        for(let key of this.configurationControls){
          if(key.name=='medal'){
            console.log("22222222")
            this.medalBol=!!key.scale;
            break;
          }
        }
      },
      //closeMedal
      closeMedal(item){
        this.medalBol=!this.medalBol;
        this.currentModal.label = item.title;
        this.currentModal.value = item.scale;
        this.currentModal.id = item.id;
        if(!this.medalBol){
          this.currentModal.value = 0;
          this.submitHandler();
        }else{
          this.currentModal.value = item.scale;
          this.editHandler(item);
        }
      },
      //修改提交配置信息
      async submitHandler() {
        try {
          if (this.currentModal.value === "") {
            this.$Message.error(`${this.currentModal.label}不能为空`);
          } else {
            const result = await this.$http.post(this.$api.update_configuration, {
              id: this.currentModal.id,
              scale: this.currentModal.value
            });
            if (result.data.result === "1") {
              this.$Message.success("修改成功");
              const findItem = this.configurationControls.find(item => {
                return item.id === this.currentModal.id;
              })
              findItem.scale = this.currentModal.value;
            } else {
              this.$Message.error(result.data.msg);
            }
            this.currentModal.show = false;
          }
        } catch (e) {
        }
      }
    }
  };
</script>
<style scoped>
  .vertical-center-modal {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .vertical-center-modal .ivu-modal {
    top: 0;
  }
</style>




